
.block {
    float: left;
    /*width: 350px;*/
    margin: 5px;
    border: 1px solid #C4C4C4;
    border-top: 0;
}

.titlebar {
    height: 25px;
    padding: 0px 5px;
    border-bottom: 1px solid #EEE;
    background: url(../../assets/round-top-center.gif) repeat-x;
}

.title {
    font-weight: normal;
    font-size: 1.4em;
}

.title_control {
    cursor: pointer;
    text-decoration: none;
}

.title_controls {
    display: none;
}

.c_delete {
    color: #CFCFCF;
}

.c_delete:hover {
    color: #777;
}

.c_edit {
    margin-right: 10px;
    color: #CFCFCF;
}

.c_edit:hover {
    color: #777;
}

.c_back {
    color: #fff;
    border-color: #1299ec;
    padding: 2px 7px;
    background: #28a3ef;
    border-radius: 3px;
}

.c_back:hover {
    background: #4cb9fc;
}

.even {
    background-color: #E9F6FF;
}

.list_row {
    padding: 1px;
}

.list_row_edit {
}

.list_row_edit:hover {
    cursor: move;
}

.list_row_add {
    cursor: pointer;
}

.list_row_add:hover {
    background: #4cb9fc;
}

.column_type {
    background: #28a3ef;
    color: #fff;
    padding: 1px 6px;
}

.column_seperator {
    margin: 0 2px;
}

.column_tail {
    color: #CFCFCF;
}

.column_tail:hover {
    /*color: #777;*/
}

.item_link {
    cursor: pointer;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 290px;
    margin: 2px 4px;
    -webkit-transition: color .2s ease-out;
    transition: color .2s ease-out;
}

a.item_link {
    color: black;
    text-decoration: none;
}

.item_link:hover {
    color: #28a3ef;
}

.item_link_edit {
    /*border: dashed 1px;*/
    cursor: move;
    background: #f7f7f7;
    border: 1px solid #dedede;
    padding: 0 7px;
}

.item_link_edit:hover {
    color: black;
}

.item_link img {
    width: 16px;
    height: 16px;
    margin-right: 2px;
}

.item_control {
    margin-left: 8px;
    cursor: pointer;
    color: #CFCFCF;
    -webkit-transition: color .2s ease-out;
    transition: color .2s ease-out;
}

.item_control:hover {
    color: #4cb9fc;
}

.i_delete {

}

.i_delete:hover {
}




