
.file-upload {
    position: relative;
    overflow: hidden;
}

.file-upload input.upload {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
}

.icon-loading {
    -webkit-animation: rotation 2s infinite linear;
            animation: rotation 2s infinite linear;
}

.icon-loading:before {
    content: "\e602;";
}



